$global-lineheight: 1.3;

$body-background: $silver;
$body-font-family: $font-family-sans;
$body-font-color: $grey;

$foundation-palette: (
  primary: $primary,
  success: $success,
  warning: $warning,
  alert: $alert,
  info: $info
);

$anchor-color: inherit;
$anchor-color-hover: inherit;

$button-sizes: (
  small: 0.85rem,
  default: 1.1rem,
  large: 1.3rem
);
$button-radius: 2em;
$button-padding: 0.75em 1em;
$button-margin: 0;

$form-label-color: inherit;
$form-label-line-height: 1;

$input-radius: 2px;
$input-font-size: 1.1rem;
$input-border: 1px solid lighten($grey-light, 12%);
$input-border-focus: 1px solid $primary;
$input-shadow: none;
$input-shadow-focus: none;

$header-font-weight: 600;
